*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}

.container {
  max-width: 1440px;

  margin: 0 auto;
  padding: 0 15px;
}

.app {
  padding-top: 50px;
}
